<template>
    <el-container v-loading="loading">
        <div class="rise x add">
            <div class="title">{{header}}</div>
            <div class="content">
                <div class="item" v-for="(item, index) in data" :key="index">
                    <span>{{item.name}}</span>
                    <el-input v-if="item.module === 'title'" v-model="title" :placeholder="item.placeholder"/>
                    <el-date-picker v-if="item.module === 'beginTime'" v-model= "beginTime" :placeholder="item.placeholder" type="date"/>
                </div>
                <picupload label="公告图片：" ref="picup" :defaultValue="picDefault"/>
                <!-- 公告内容 -->
                <div class="item">
                    <span>公告内容：</span>
                    <el-input type="textarea" :rows="4" placeholder="请输入公告内容" v-model="content" />
                </div>
            </div>
            <div class="btn-list">
                <el-button @click="cancle()" class="btn">取消</el-button>
                <el-button @click="save()" class="btn">保存</el-button>
            </div>
        </div>
    </el-container>
</template>

<script>
import queryString from "querystring";
import { DateDay } from "utils/formData";
import Picupload from "components/picupload";
const headers = ["新建", "编辑"];
const defaultV = [
  { name: "公告标题：", placeholder: "请输入公告标题", module: "title" }
  // { name:'公告时间：', placeholder:'请选择公告时间', module:'beginTime', type:'date'},
];

export default {
  name: "add",
  data() {
    return {
      title: "",
      id: "",
      header: "",
      type: "",
      content: "",
      picDefault: null, //默认地址
      data: defaultV,
      loading: false
    };
  },
  components: {
    Picupload
  },
  mounted() {
    this.dataInit();
    // this.dropInit();
  },
  methods: {
    dataInit() {
      const { type, id } = this.$route.query;
      this.type = type;
      if (type !== "edit") return (this.header = headers[0]);
      this.header = headers[1];
      this.id = id;
      this.detailAjax();
    },
    editInit(res) {
      const { title, content, pics } = res.data;
      this.title = title;
      this.content = content;
      this.picDefault = pics;
    },
    detailAjax() {
      //查看详情
      this.loading = true;
      this.api.get(`/notice/${this.id}`).then(res => {
          this.loading = false;
          this.editInit(res);
        })
        .catch(err => {
          console.log(err);
          this.loading = false;
          this.$message(err.message);
        });
    },
    editAjax() {
      const params = {
        title: this.title,
        content: this.content,
      };
      params.pics = this.$refs.picup.getData();
      const axiosType = ["post", "put"];
      let type = axiosType[0];
      if (this.type === "edit") {
        params.id = this.id;
        type = axiosType[1];
      }
      this.loading = true;
      this.api[type](`/notice`, params, "form").then(res => {
          this.loading = false;
          this.$router.go(-1);
        })
        .catch(err => {
          this.loading = false;
          console.log(err);
          this.$message(err.message);
        });
    },
    save() {
      this.editAjax();
    },
    cancle() {
      //取消
      this.$router.go(-1);
    }
  }
};
</script>
 
<style lang="less" scoped>
</style>
 